@mixin bp-desktop {
  @media screen and (min-width: 1600px) {
    @content; } }

@mixin bp-table {
  @media screen and (max-width: 768px) {
    @content; } }

@mixin bp-mob {
  @media screen and (max-width: 480px) {
    @content; } }

@mixin bp-landscape {
  @media screen and (max-device-width: 768px) and (orientation: landscape) {
    @content; } }

@mixin transition($args...) {
  transition: $args; }
